define('ch-ember-app/utils/publish-settings', ['exports'], function (exports) {
  exports.addPublishSettings = addPublishSettings;
  /**
   * Adds the available publish settings to the data store.
   * @method addPublishSettings
   * @public
   * @param {DS.Store} store The ember-data store for this application
   * @param {Service}  intl  The internationalization service
   */

  function addPublishSettings(store, intl) {
    store.createRecord('publish-setting', { id: 'none', name: intl.t('filter.publishSettings.none.title') });
    store.createRecord('publish-setting', { id: 'import', name: intl.t('filter.publishSettings.import.title'), description: intl.t('filter.publishSettings.import.description') });
    store.createRecord('publish-setting', { id: 'publish', name: intl.t('filter.publishSettings.publish.title'), description: intl.t('filter.publishSettings.publish.description') });
  }
});