define('ch-ember-app/instance-initializers/ch-logger-bugsnag', ['exports'], function (exports) {
  exports['default'] = {
    name: 'ch-logger-bugsnag',
    after: 'ember-logging-bugsnag',
    initialize: function initialize(applicationInstance) {
      // Create the application-specific context that can parse out the relevant
      // values from the context returned by the context service.
      var contextCallback = function contextCallback(context) {
        return {
          url: context.application.url,
          projectRoot: context.application.urlHash,
          appVersion: context.application.version,
          file: context.application.route,
          userId: '',
          userAgent: context.user.userAgent,
          language: context.user.language,
          metaData: {
            contentHub: {
              clientId: context.application.clientId,
              contentHubHost: context.application.contentHubHost,
              apiKey: context.application.apiKey
            },
            routeParams: context.application.routeParams
          }
        };
      };

      var consumer = applicationInstance.lookup('service:bugsnagLoggingConsumer');
      consumer.set('applicationContextCallback', contextCallback);
    }
  };
});