define('ch-ember-app/instance-initializers/ch-logging-service', ['exports'], function (exports) {
  // Returns application and user context for the logging service based on the
  // configuration.
  exports['default'] = {
    name: 'ch-logging-service',
    after: 'ember-logging-service',
    initialize: function initialize(applicationInstance) {
      var logger = applicationInstance.lookup('service:logger');
      var context = applicationInstance.lookup('service:contextService');

      // Register the Lift application and user context with the logger service.
      logger.registerApplicationContextCallback('ch-ember-app', [context.getApplicationContext, context]);
      logger.registerUserContextCallback('ch-ember-app', [context.getUserContext, context]);
    }
  };
});